
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState, mapActions, mapGetters } from 'vuex'
import { go } from 'vux/src/libs/router'

export default {
  components: {
    Swipeout,
    SwipeoutItem,
    SwipeoutButton,
    Cell
  },
  computed: {
    ...mapGetters([
      "selectedCities"
    ]),
  },
  methods: {
    ...mapActions([
      'changeCity',
      'deleteCity',
    ]),
    changeCityClick(item) {
      go('/', this.$router)
      this.changeCity({ coordinates: item.coordinates, cityName: item.name })
    },
    moreCity() {
      go('/more', this.$router)
    },

  }
}




